import Vue from "vue";
import VueRouter from "vue-router";
import App from "./App.vue";
import router from "./router";
import {
  Button,
  Select,
  Input,
  Image,
  Slider,
  Dialog,
  Option,
  Popover,
} from "element-ui";

Vue.use(Button);
Vue.use(Input);
Vue.use(Select);
Vue.use(Image);
Vue.use(Slider);
Vue.use(Dialog);
Vue.use(Option);
Vue.use(Popover);

Vue.config.productionTip = false;
Vue.use(VueRouter);
new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
